*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    
}
html{
    font-size: 62.5%;
}
.hash-field{
    border: 0.1rem solid #696969;
    width: 85rem;
    margin: 1rem auto;
    padding: 1rem;
    border-radius: 1.6rem;
    background-color: #fff7cc;
    position: relative;
    overflow: hidden;
    
}
.hash-field::before{
    content: "Simulator";
    background-color: #ffc04d;
    color: #fff;
    font-size: 2rem;
    padding: 0.5rem 4rem;
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(-27%, 58%) rotate(-42deg);
}
.hash-field:hover{
    box-shadow: 0 0 1.5rem 0 #332b00;
}
.output{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 0.8rem;
}
.output-field{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}
.copy-icon{
    width: 3.2rem;
    height: 3.2rem;
    stroke: #806c00;
    cursor: pointer;
}
.copied-text{
    color: green;
    font-size: 1.6rem;
    font-weight: bold;
}
.input{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 0.8rem;
    margin-bottom: 1rem;
}
.input label{
    font-size: 2.4rem;
    font-weight: bold;
}
.input textarea{
    width: 60%;
    height: 18.6rem;
    padding: 0.8rem;
    border-radius: 0.8rem;
    border: 1px solid #696969;
    background-color: #cccccc;
    color: #000;
    font-size: 1.6rem;

}

.output .text{
    font-size: 2.4rem;
    font-weight: bold;
}
.output p{
    border-radius: 0.5rem;
    border: 0.5px solid #696969;
    padding: 0.6rem 0.8rem;
    background-color: #fff3b3;
    font-size: 1.6rem;
    color: #000;
}
.output button{
    padding: 0.5rem 3rem;
    font-size: 2.4rem;
    font-weight: bold;
    margin-top: 1rem;
    background-color: #ffe766;
    border-radius: 0.8rem;
    border: 0.1rem solid #806c00;

}
.output button:hover ,
.output button:active{
    background-color: #e6c200;
    color: #fff;
    box-shadow: 0 0 1rem 0.2rem #ffd700;
    border: #ffd700;

}