*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 1.6rem;
}
html{
    font-size: 62.5%;
    background-color: #ffedcc;
}
.stimulator-box{
    margin-bottom: 8rem;
}
header{
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    background-color: #ffc966;
    margin-bottom: 1.6rem;
    height: 7.4rem;
}
header h1{
    font-size: 3.6rem;
    padding: 0 1rem;
}
.header-nav{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 100%;
}
.header-btn{
    height: 100%;
    font-size: 2rem;
    padding: 0 1.2rem;
    border: none;
    background-color: #ffc966;
}
.header-btn:hover{
    background-color: #ffa500;
}
.topic{
    text-align: center;
    margin-bottom: 2rem;
    font-size: 2.4rem;
}
.video-box{
    width: 88rem;
    margin: 0 auto;
    display: flex;
    align-items: center;
}
iframe{
    width: 128rem;
    height: 48rem;
    margin: 1.6rem auto;
    border: 1px solid #4c3100;
}
footer{
    background-color: #e1e1e1;
    margin: 1.6rem 0 0 0;
    padding: 3.2rem 5rem;
    height: 32rem;
    display: flex;
    align-items: center;
    justify-content: center;
    
}
.footer-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    column-gap: 7.4rem;
}
.footer-heading{
    font-size: 2.2rem;
    margin-bottom: 0.5rem;
}
.footer-link{
    text-decoration: none;
    color: #000;
}
.footer-nav{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
address{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.footer-nav li{
    list-style-type: none;
    font-size: 1.4rem;
}
.logo-col{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.address-col, .Account-col, .Company-col{
    display: flex;
    flex-direction: column;
    gap: 2rem;
}
