*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.section-2{
    border: 1px solid #696969;
    width: 90%;
    margin: 0 auto 2rem auto;
    padding: 1.6rem;
    border-radius: 0.8rem;
    background-color: #fff7cc;
}
.section-2:hover{
    box-shadow: 0 0 1.5rem 0 #332b00;
}
.section-2-1{
    margin: 0 0 0.5rem 0;
    
}
.section-2-1 h2{
    font-size: 2.4rem;
    line-height: 1.2;
    word-spacing: 0.2rem;
}
.section-2-1 p{
    font-size: 1.8rem;
    line-height: 1.2;
    word-spacing: 0.2rem;
    margin: 1rem 0 2rem 2rem;
}
.section-2-2 h3, .section-2-3 h3{
    font-size: 2.4rem;
    line-height: 1.2;
    word-spacing: 0.2rem;
}
.section-2-2 ul, .section-2-3 ol{
    margin: 1rem 0 2rem 7.4rem;
}
.section-2-2 ul li, .section-2-3 ol li{
    font-size: 1.8rem;
    line-height: 1.2;
    word-spacing: 0.2rem;
    /* margin: 1rem 0 2rem 8rem; */
    margin-bottom: 2rem;
}