@media screen and (max-width: 59em){
    header{
        display: grid;
        grid-template-columns: auto 1fr;
        padding: 1rem 1.6rem;
        position: relative;
        height: auto;
    }
    .header-nav{
        display: none;
    }
    header h1{
        word-wrap: break-word;
        /* text-align: center; */
    }
    header .mobile-nav-container{
        display: block;
        z-index: 8;
    }
    .mobile-nav-btn{
        display: flex;
        align-items: center;
        justify-content: center;
        border: 0.1rem solid #b37300;
    }
    .mobile-nav-btn svg{
        width: 3.6rem;
        height: 3.6rem;
    }
    .close-btn{
        position: absolute;
        border: 0.1rem solid #332100;
        top: 1.6rem;
        right: 1.6rem;
    }
    .header-nav-mov{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        width: 100%;
        height: 100vh;
        gap: 3rem;
        background-color: #fff;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -2;
    }
    .header-btn-mov{
        font-size: 3.2rem;
        font-weight: bold;
        border: none;
        background: none;
        padding: 1rem 2rem;
        height: 10%;
    }
    .header-btn-mov:hover{
        background: none;
        transform: scale(1.2);
        transition: all, 0.2s;
    }
    .header-btn-mov:last-child{
        background-color: #ffc04d;
        color: #332100;
        border-radius: 2rem;
    }
    .hash-field{
        width: 95%;        
    }
    .output-field{
        display: flex;
        flex-direction: column;
        width: 100%;
    }
    .output p{
        width: 100%;
        word-wrap: break-word;
        text-align: center;
    }

    .video-box{
        width: 100%;
        height: 40vh;
        margin: 0 auto;
        display: flex;
        align-items: center;
    }
    iframe{
        width: 86%;
        height: 100%;
        margin: 1.6rem auto;
        border: 1px solid #4c3100;
    }
    footer{
        margin: 2rem 0 0 0;
        padding: 0.2rem 1.6rem;
        height: 37rem;
        
    }
    .footer-container{
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 3.2rem;
        row-gap: 3.2rem;
    }
    .footer-heading{
        font-size: 2.2rem;
        margin-bottom: 0;
    }
    .footer-link{
        text-decoration: none;
        color: #000;
    }
    .footer-nav{
        display: flex;
        flex-direction: column;
        gap: 0.2rem;
    }
    address{
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }
    .footer-nav li{
        list-style-type: square;
        font-size: 1.4rem;
    }
    .logo-col{
        display: flex;
        flex-direction: column;
        gap: 2rem;
    }
    .address-col, .Account-col, .Company-col{
        display: flex;
        flex-direction: column;
        gap: 2rem;
    }
    
}