*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.codeBox{
    width: 70%;
    height: 70vh;
    margin: 1rem auto;
    background-color: #ffdb99;
    border-radius: 0.4rem;
    border: 0.8px solid #4c3100;
    overflow: auto;
    
}
.codeBox:hover{
    box-shadow: 0 0 1.2rem 0 #191000;
}
.code-heading{
    display: grid;
    grid-template-columns: 1fr 25%;
    align-items: center;
    background-color: #ffa500;
    gap: 1rem;
}
.copy-btn{
    display: flex;
    align-items: center;
    padding: 0 0.2rem;
    justify-self: end;
    background-color: #ffe4b3;
    border: 1px solid #ffa500;
}
.copied-btn{
    color: #008000;
    display: flex;
    align-items: center;
    padding: 0 0.2rem;
    justify-self: end;
    background-color: #ffe4b3;
    border: 1px solid #ffa500;
    margin: 0;
}
.copy-icon{
    width: 3.2rem;
}
.cpp-code, .java-code, .python-code{
    color: #008000;
    font-size: 1.6rem;
    overflow: auto;
}
.cpp-box, .java-box, .python-box{
    padding: 0.1rem 0.2rem;
}

.lan-btn{
    display: flex;
    height: 100%;
    align-items: stretch;
    overflow: hidden;
}

.active-btn{
    padding: 0.5rem 1rem;
    background-color: #ffdb99;    
    border: none;
}

.passive-btn{
    padding: 0.5rem 0.8rem;
    border-radius: 0.1rem;
    border: none;
    background-color: #ffa500;
}